<template>
  <div>
    <div>
      <v-alert
          v-model="alert"
          border="left"
          colored-border
          dismissible
          transition="fade-transition"
          elevation="2"
          type="warning"
      >
        belum siap untuk menyimpan, mohon tunggu <strong>5</strong> detik lagi, lalu coba kembali
      </v-alert>
    </div>
    <v-card color="#7fb901" flat tile>
      <v-container class="" fluid>
        <v-row class="" no-gutters style="position: relative">
          <v-col class="d-flex justify-center" style="position: absolute"><h2 class="white--text font-weight-medium">
            Detail Kontak</h2>
          </v-col>
          <v-col
              class="d-flex justify-start" style="z-index: 1; position: relative;">
            <v-icon
                color="white"
                dense
                style="cursor:pointer; "
                x-large
                @click="backToHome"
            >mdi-chevron-left
            </v-icon>
            <h2 class="white--text" style="cursor:pointer;" @click="backToHome">Back</h2>
          </v-col>
        </v-row>

      </v-container>
    </v-card>
    <v-list
        class="overflow-y-auto px-2"
        style="max-height: 80vh"
    >
      <v-container fluid>
        <v-row>
          <v-col>
            <v-card class="pa-3 mx-2 my-0 rounded-b-lg" color="#FDF5E5" outlined>
              <h4 style="color: #B97B00;">Pengingat</h4>
              <h5 class="font-weight-regular" style="color: #B97B00;">1.Mohon selalu isi, tambahkan atau perbaharui
                secara
                lengkap "semua data".</h5>
              <h5 class="font-weight-regular" style="color: #B97B00;">2.Mohon hanya mengerjakan kontak didalam lebel
                "Penugasan Ke" yang disesuaikan dengan nama grup yang ditugaskan kepada grup Sdr/i.</h5>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col class="d-flex my-4 ml-auto justify-center" width="100%">
            <v-avatar
                color="#f5f5f5"
                size="160">
              <v-icon
                  color="black"
                  size="128"
              >mdi-account-multiple-outline
              </v-icon>
            </v-avatar>
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <v-card v-if="detailKontak" class="pa-0 mx-0 my-0 rounded-b-lg" flat>
              <h3 style="color: black;">Kontak ini Dikerjakan Oleh Penyiar:</h3>
              <h4 class="font-weight-medium grey--text text--darken-2">{{ detailKontak.name }}</h4>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <v-card class="pa-0 mx-0 my-0 rounded-b-lg d-flex justify-space-between" flat>
              <h5 class="font-weight-medium grey--text text--darken-2">Pilihlah Nama Saudara/i:</h5>
              <h6 class="font-weight-medium grey--text text--lighten-1">Required</h6>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col v-if="detailKontak && detailKontak.name !== null">
            <DialogFilter
                :filter-selected="filterSelected"
            />
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <v-card class="pa-0 mx-0 my-0 rounded-b-lg d-flex justify-space-between" flat>
              <h5 class="font-weight-medium grey--text text--darken-2">Pilihlah Jenis Pengabaran untuk Kontak Ini</h5>
              <h6 class="font-weight-medium grey--text text--lighten-1">Required</h6>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col v-if="detailKontak && detailKontak.name != null">
            <div v-if="detailKontak.jenis === 'TELEPON'">
              <v-btn
                  class="mr-2"
                  color="#7fb901"
                  elevation="2"
                  rounded
              ><h3 class="white--text">TELEPON</h3></v-btn>
              <v-btn
                  color="#f2f2f2"
                  elevation="2"
                  rounded
                  @click="updateJenis('SURAT')"
              ><h3 class="font-weight-regular white--black">SURAT</h3></v-btn>
            </div>
            <div v-if="detailKontak.jenis === 'SURAT'">
              <v-btn
                  class="mr-2"
                  color="#f2f2f2"
                  elevation="2"
                  rounded
                  @click="updateJenis('TELEPON')"
              ><h3 class="font-weight-regular white--black">TELEPON</h3></v-btn>
              <v-btn
                  color="#7fb901"
                  elevation="2"
                  rounded
              ><h3 class="white--text">SURAT</h3></v-btn>
            </div>
          </v-col>
        </v-row>
        <v-row>
          <v-col v-if="detailKontak && detailKontak.namaPemilikusaha !== null">
            <v-card class="pa-0 mx-0 my-0 rounded-b-lg d-flex justify-space-between" flat>
              <div>
                <h5 class="font-weight-regular grey--text text--darken-2">Nama Pemilik Usaha / Rumah</h5>
                <h5 class="font-weight-medium black--text">{{ detailKontak.namaPemilikusaha }}</h5>
              </div>
              <v-avatar color="#F0F7e1">
                <v-icon color="#7FB901">mdi-home</v-icon>
              </v-avatar>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col v-if="detailKontak && detailKontak.nomorTelepon !== null">
            <v-card class="pa-0 mx-0 my-0 rounded-b-lg d-flex justify-space-between" flat>
              <div>
                <h5 class="font-weight-regular grey--text text--darken-2">Nomor Telepon</h5>
                <h5 class="font-weight-medium black--text">{{ detailKontak.nomorTelepon }}</h5>
              </div>
              <a :href="`tel:${detailKontak.nomorTelepon}`" class="text-decoration-none">
                <v-avatar color="#F0F7e1">
                  <v-icon color="#7FB901">mdi-phone</v-icon>
                </v-avatar>
              </a>

            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col v-if="detailKontak && detailKontak.alamat !== null">
            <v-card class="pa-0 mx-0 my-0 rounded-b-lg d-flex justify-space-between" flat>
              <div>
                <h5 class="font-weight-regular grey--text text--darken-2">Alamat</h5>
                <h5 class="font-weight-medium black--text">{{ detailKontak.alamat }}</h5>
              </div>

            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col v-if="detailKontak && detailKontak.alamat !== null">
            <v-card class="pa-0 mx-0 my-0 rounded-b-lg d-flex flex-column" flat>
              <h5 class="font-weight-regular grey--text text--darken-2">Pilih Tanggal Dikerjakan</h5>
              <v-dialog
                  ref="dialog"
                  v-model="dateDialog"
                  persistent
                  width="290px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                      :value="formatDateDialog"
                      background-color="#ececec"
                      class="black--text"
                      label="Masukkan Tanggal hari ini..."
                      prepend-inner-icon="mdi-calendar"
                      readonly
                      solo
                      v-bind="attrs"
                      v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                    v-model="tanggalDikerjakan"
                    scrollable
                >
                  <v-spacer></v-spacer>
                  <v-btn
                      color="primary"
                      text
                      @click="dateDialog = false"
                  >
                    Cancel
                  </v-btn>
                  <v-btn
                      color="primary"
                      text
                      @click="updateTanggalDikerjakan(tanggalDikerjakan)"
                  >
                    OK
                  </v-btn>
                </v-date-picker>
              </v-dialog>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col v-if="detailKontak && detailKontak.topikYangDibagikan !== null">
            <v-card class="pa-0 mx-0 my-0 rounded-b-lg d-flex flex-column" flat>
              <div>
                <h5 class="mb-2 font-weight-regular grey--text text--darken-2">Topik yang Dibagikan</h5>
                <v-textarea
                    v-model="detailKontak.topikYangDibagikan"
                    background-color="#ececec"
                    class="black--text"
                    label="Ketik topik dan ayat yang disurati/dibahas saat menelepon..."
                    outlined
                    solo
                    @change="updateTopikYangDibagikan(detailKontak.topikYangDibagikan)"
                ></v-textarea>
              </div>

            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <v-card class="pa-0 mx-0 my-0 rounded-b-lg d-flex justify-space-between" flat>
              <h5 class="font-weight-medium grey--text text--darken-2">Tanggapan Kontak</h5>
              <h6 class="font-weight-medium grey--text text--lighten-1">Required</h6>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <TanggapanKontakFilter
                :filter-selected="filterSelectedTanggapanKontak"
            />
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <v-card class="pa-0 mx-0 my-0 rounded-b-lg d-flex justify-space-between" flat>
              <h5 class="font-weight-medium grey--text text--darken-2">KONFIRMASI PENYIAR (kepada kontak ini)</h5>
              <h6 class="font-weight-medium grey--text text--lighten-1">Required</h6>
            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <KonfirmasiFilter
                :filter-selected="filterSelectedKonfirmasi"
            />
          </v-col>
        </v-row>
        <v-row>
          <v-col v-if="detailKontak && detailKontak.komentar !== null">
            <v-card class="pa-0 mx-0 my-0 rounded-b-lg d-flex flex-column" flat>
              <div>
                <h5 class="mb-2 font-weight-regular grey--text text--darken-2">Mohon Selalu Berikan Catatan untuk Kontak ini Kapanpun DIkerjakan:</h5>
                <v-textarea
                    v-model="detailKontak.komentar"
                    background-color="#ececec"
                    class="black--text"
                    label="Misalnya dihubungi kali ke 2, topik kunjungan kembali, tanggal janjian, khususnya perubahan tanggapan kontak menjadi menolak, dsb..."
                    outlined
                    solo
                    @change="updateKomentar(detailKontak.komentar)"
                ></v-textarea>
              </div>

            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <v-card class="pa-0 mx-0 my-0 rounded-b-lg d-flex justify-space-between" flat>
              <div>
                <h5 class="font-weight-regular grey--text text--darken-2">Kode Daerah</h5>
                <h5 class="font-weight-medium black--text">B15, No. 1</h5>
              </div>
              <a :href="`tel:${detailKontak.nomorTelepon}`" class="text-decoration-none">
                <v-avatar color="#F0F7e1">
                  <v-icon color="#7FB901">mdi-phone</v-icon>
                </v-avatar>
              </a>

            </v-card>
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <v-card class="pa-0 mx-0 my-0 rounded-b-lg d-flex justify-space-between" flat>
              <v-chart class="chart" :option="option" autoresize />
            </v-card>
          </v-col>
        </v-row>

      </v-container>

      <div style="height: 50px"></div>
    </v-list>
  </div>
</template>

<script>

import moment from "moment";
import mixin from "@/mixin";
import DialogFilter from "@/views/DetailKontak/DialogFilter";
import TanggapanKontakFilter from "@/views/DetailKontak/TanggapanKontakFilter";
import KonfirmasiFilter from "@/views/DetailKontak/KonfirmasiFilter";
import {mapGetters} from "vuex";
import store from "@/store";

import { use } from 'echarts/core';
import { CanvasRenderer } from 'echarts/renderers';
import { BarChart, LineChart } from 'echarts/charts';
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
  GridComponent,
  ToolboxComponent
} from 'echarts/components';
import VChart, { THEME_KEY } from 'vue-echarts';

use([
  CanvasRenderer,
  BarChart,
  LineChart,
  
  TitleComponent,
  TooltipComponent,
  ToolboxComponent,
  LegendComponent,
  GridComponent
]);

export default{
  name: "Home",
      provide: {
    [THEME_KEY]: 'light',
  },
   data() {
    return {

      alert: false,
      listDataDinas: [],
      search: "",
      emptyList: [],
      dateDialog: false,
      tanggalDikerjakan: null,
    }
  },
  computed: {
    ...mapGetters({
      lastTelepon: 'base/lastTelepon',
      lastSurat: 'base/lastSurat',
      listPenyiar: 'base/listPenyiar',
      listPenyiarDetailKontak: `base/listPenyiarDetailKontak`,
      listTeleponSurat: 'base/listTeleponSurat',
      titleTeleponSurat: 'base/titleTeleponSurat',
      listJenisTanggapan: `base/listJenisTanggapan`,
      listChartData: `base/listChartData`,
      detailKontak: `base/detailKontak`,
      saveReady: `base/saveReady`,

    }),
    option(){
      return {
        title: {
          text: 'Grafik Tanggapan Kontak',
              left: 'center',
        },

        tooltip: {
          trigger: 'item'
        },
        legend: {
          bottom: 0,
              itemHeight: 8, itemWidth: 8,
              // data: ['Menunggu Dihubungi (Surat)', 'Nomor Salah / Tidak Terdaftar', 'Nomor Tidak Aktif', 'Menerima', 'Telepon Tidak Diangkat', 'Menolak', 'Sibuk'],
              data: this.listChartData.map(x => x.name)
        },
        xAxis: {
          type: 'category',
              data: ['Jumlah'],

        },
        yAxis: {
          type: 'value'
        },
        color: ['#fee202', '#e52e1f', '#ff7527', '#d356b5', '#289fff', '#57d6c0', '#fdaed0' ],
            //   series: [
            //     {
            //       name: 'Menunggu Dihubungi (Surat)',
            //       type: 'bar',
            //       data: [335]
            //     },
            //     {
            //       name: 'Nomor Salah / Tidak Terdaftar',
            //       type: 'bar',
            //       data: [30]
            // },
            //     {
            //       name: 'Nomor Tidak Aktif',
            //       type: 'bar',
            //       data: [769]
            //     },
            //     {
            //       name: 'Menerima',
            //       type: 'bar',
            //       data: [693]
            //     },
            //     {
            //       name: 'Telepon Tidak Diangkat',
            //       type: 'bar',
            //       data: [1100]
            //     },
            //     {
            //       name: 'Menolak',
            //       type: 'bar',
            //       data: [260]
            //     },
            //     {
            //       name: 'Sibuk',
            //       type: 'bar',
            //       data: [489]
            //     },
            //   ],
            series: this.listChartData,
            toolbox: {
          show: true,
              feature: {
            dataView: {show: true, readOnly: false},
            magicType: {show: true, type: ['line', 'bar']},
            restore: {show: true},
            saveAsImage: {show: true}
          },
          top: "0px",
              right: "0px"
        },
      }
    },
    formatDateDialog() {
      moment.locale('id')
      if (this.detailKontak.tanggalDikerjakan) {
        if (this.detailKontak.tanggalDikerjakan !== "-") {
          return moment(this.detailKontak.tanggalDikerjakan).hour(0).minute(0).second(0).format('DD MMMM YYYY')
        }
      }
      return ""
    },
  },
  // watch: {
  //     listPenyiar() {
  //       console.log("gk kelamaan apa ya")
  //       this.filterData();
  //   }
  // },
  methods:
  
      {
        backToHome() {
          this.$router.push('/')
        },
        filterData() {
          let loadDataDinas = mixin.methods.storageLoad("listDataDinas");
          if (loadDataDinas) {
            let penyiarTerpilih = this.listPenyiar ? this.listPenyiar.filter(x => x.isChecked).map(x => x.name) : [];
            let jenisTanggapanTerpilih = this.listJenisTanggapan ? this.listJenisTanggapan.filter(x => x.isChecked).map(x => x.name) : [];
            let loadDataDinasPenyiar = [];
            let loadDataDinasTanggapan = [];
            this.storageSave("listTeleponSurat", loadDataDinas.slice(0, 100));
            if (this.search) {
              loadDataDinas = loadDataDinas.filter(x => x.namaPemilikusaha.toLowerCase().includes(this.search.toLowerCase()));
            }
            if (penyiarTerpilih.length > 0)
              penyiarTerpilih.forEach(x => {
                loadDataDinasPenyiar = [...new Set([...loadDataDinasPenyiar, ...loadDataDinas.filter(xx => xx.name === x)])];
              });
            else loadDataDinasPenyiar = loadDataDinas

            if (jenisTanggapanTerpilih.length > 0) {
              jenisTanggapanTerpilih.forEach(x => {
                loadDataDinasTanggapan = [...new Set([...loadDataDinasTanggapan, ...loadDataDinasPenyiar.filter(xx => xx.jenisTanggapan === x)])];
              });
            } else loadDataDinasTanggapan = loadDataDinasPenyiar
            loadDataDinasTanggapan = loadDataDinasTanggapan.filter(x => x.tanggalDikerjakan !== "-").sort(function (a, b) {
              return new Date(b.tanggalDikerjakan) - new Date(a.tanggalDikerjakan);
            })

            let teleponSurat = this.titleTeleponSurat ? this.titleTeleponSurat : "TELEPON";
            this.$store.commit("base/listTeleponSurat", loadDataDinasTanggapan.filter(x => x.jenis.toLowerCase() === teleponSurat.toLowerCase()).slice(0, 100));
          }
        },
        formatDate(date) {
          return date !== "-" ? moment(date).format('DD MMMM YYYY') : "-";
        },
        saveNotReady(){
          if (!this.saveReady) {
            this.dateDialog = false;
            this.alert = true;
            window.setInterval(() => {
              this.alert = false;
            }, 5000)
            return true;
          }
          return false;
        },
        async updateTanggalDikerjakan(selected) {
          if(this.saveNotReady()) return;
          moment.locale('id')
          this.tanggalDikerjakan = selected
          this.dateDialog = false;
          let loadDataDinas = mixin.methods.storageLoad("listDataDinas");
          if (loadDataDinas) {
            let detailKontakTerpilih = loadDataDinas.filter(x => x.key === this.detailKontak.key);
            if (detailKontakTerpilih) {
              let doc = store.getters["base/doc"]
              if (doc) {
                const sheet = doc.sheetsByTitle["Data Kontak Dinas Sidang"];
                let updateCell = sheet.getCell(this.detailKontak.key, 12);
                console.log(updateCell.effectiveFormat)
                updateCell.numberFormat = {
                  type: "DATE",
                  pattern: "yyy-mm-ddThh:mm:ss.000Z",
                }
                updateCell.value = moment(this.tanggalDikerjakan).format("YYYY-MM-DDTHH:mm:ss.000") + 'Z'
                await sheet.saveUpdatedCells();
                this.detailKontak.tanggalDikerjakan = moment(this.tanggalDikerjakan).format("YYYY-MM-DDTHH:mm:ss.000") + 'Z'
                this.storageSave("detailKontak", this.detailKontak);
                this.vuexFromStorage("detailKontak");
                console.log("Berhasil update")
              }
            }
          }

        },
        async updateJenis(selected) {
          if(this.saveNotReady()) return;
          this.detailKontak.jenis = selected;
          let loadDataDinas = mixin.methods.storageLoad("listDataDinas");
          if (loadDataDinas) {
            let detailKontakTerpilih = loadDataDinas.filter(x => x.key === this.detailKontak.key);
            if (detailKontakTerpilih) {
              let doc = store.getters["base/doc"]
              if (doc) {
                const sheet = doc.sheetsByTitle["Data Kontak Dinas Sidang"];
                let updateCell = sheet.getCell(this.detailKontak.key, 4);
                updateCell.value = selected;
                await sheet.saveUpdatedCells();
                this.storageSave("detailKontak", this.detailKontak);
                this.vuexFromStorage("detailKontak");
                console.log("Berhasil update")
              }
            }
          }
        },
        async updateTopikYangDibagikan(selected) {
          if(this.saveNotReady()) return;
          this.detailKontak.topikYangDibagikan = selected;
          let loadDataDinas = mixin.methods.storageLoad("listDataDinas");
          if (loadDataDinas) {
            let detailKontakTerpilih = loadDataDinas.filter(x => x.key === this.detailKontak.key);
            if (detailKontakTerpilih) {
              let doc = store.getters["base/doc"]
              if (doc) {
                const sheet = doc.sheetsByTitle["Data Kontak Dinas Sidang"];
                let updateCell = sheet.getCell(this.detailKontak.key, 10);
                updateCell.value = selected;
                await sheet.saveUpdatedCells();
                this.storageSave("detailKontak", this.detailKontak);
                this.vuexFromStorage("detailKontak");
                console.log("Berhasil update")
              }
            }
          }
        },
        
        async updateKomentar(selected) {
          if(this.saveNotReady()) return;
          this.detailKontak.komentar = selected;
          let loadDataDinas = mixin.methods.storageLoad("listDataDinas");
          if (loadDataDinas) {
            let detailKontakTerpilih = loadDataDinas.filter(x => x.key === this.detailKontak.key);
            if (detailKontakTerpilih) {
              let doc = store.getters["base/doc"]
              if (doc) {
                const sheet = doc.sheetsByTitle["Data Kontak Dinas Sidang"];
                let updateCell = sheet.getCell(this.detailKontak.key, 14);
                updateCell.value = selected;
                await sheet.saveUpdatedCells();
                this.storageSave("detailKontak", this.detailKontak);
                this.vuexFromStorage("detailKontak");
                console.log("Berhasil update")
              }
            }
          }
        },

        async filterSelected(selected) {
          if(this.saveNotReady()) return;
          let loadDataDinas = mixin.methods.storageLoad("listDataDinas");
          if (loadDataDinas) {
            let detailKontakTerpilih = loadDataDinas.filter(x => x.key === this.detailKontak.key);
            if (detailKontakTerpilih) {
              let doc = store.getters["base/doc"]
              console.log("load doc")
              if (doc) {
                const sheet = doc.sheetsByTitle["Data Kontak Dinas Sidang"];
                let updateCell = sheet.getCell(this.detailKontak.key, 2);
                updateCell.value = selected.name;
                await sheet.saveUpdatedCells();
                this.detailKontak.name = selected.name;
                this.storageSave("detailKontak", this.detailKontak);
                this.vuexFromStorage("detailKontak");
                console.log("Berhasil update")
              }
            }
          }
        },
        async filterSelectedTanggapanKontak(selected) {
          if(this.saveNotReady()) return;
          let loadDataDinas = mixin.methods.storageLoad("listDataDinas");
          if (loadDataDinas) {
            let detailKontakTerpilih = loadDataDinas.filter(x => x.key === this.detailKontak.key);
            if (detailKontakTerpilih) {
              let doc = store.getters["base/doc"]
              console.log("load doc")
              if (doc) {
                const sheet = doc.sheetsByTitle["Data Kontak Dinas Sidang"];
                let updateCell = sheet.getCell(this.detailKontak.key, 9);
                updateCell.value = selected.tanggapan;
                await sheet.saveUpdatedCells();
                this.detailKontak.jenisTanggapan = selected.tanggapan;
                this.storageSave("detailKontak", this.detailKontak);
                this.vuexFromStorage("detailKontak");
                console.log("Berhasil update")
              }
            }
          }
        },
        async filterSelectedKonfirmasi(selected) {
          if(this.saveNotReady()) return;
          let loadDataDinas = mixin.methods.storageLoad("listDataDinas");
          if (loadDataDinas) {
            let detailKontakTerpilih = loadDataDinas.filter(x => x.key === this.detailKontak.key);
            if (detailKontakTerpilih) {
              let doc = store.getters["base/doc"]
              console.log("load doc")
              if (doc) {
                const sheet = doc.sheetsByTitle["Data Kontak Dinas Sidang"];
                let updateCell = sheet.getCell(this.detailKontak.key, 13);
                updateCell.value = selected.konfirmasi;
                await sheet.saveUpdatedCells();
                this.detailKontak.konfirmasi = selected.konfirmasi;
                this.storageSave("detailKontak", this.detailKontak);
                this.vuexFromStorage("detailKontak");
                console.log("Berhasil update")
              }
            }
          }
        },
      },
  async mounted() {
    moment.locale('id');
    this.filterData();
    if (!this.detailKontak) await this.$router.push('/');
    console.log(this.detailKontak);

  },


  components: {DialogFilter, TanggapanKontakFilter, KonfirmasiFilter, VChart}
};
</script>
